.tasks {
  margin-top: 10px;
  text-align: center;
}

.task {
  cursor: pointer;
  max-width: 300px;
  background-color: white;
  margin: 10px;
  padding: 5px;
  box-shadow: 2px 2px 10px 0px rgba(0,0,0,0.5);
  margin-left: auto;
  margin-right: auto;
}

.taskForm {
  background-color: white;
  position: fixed;
  top: 20px;
  bottom: 20px;
  left: 20px;
  right: 20px;
}

.taskForm .closeButton {
  cursor: pointer;
  display: inline-block;
  margin: 5px;
  padding: 5px;
  background-color: #204b77;
  text-decoration: none;
  color: white;
}

.taskForm .closeButton:hover {
  background-color: #527aa2;
}